'use client';
import { useRouter } from 'next/navigation';
import { useEffect, useRef, useState } from 'react';

const BASE_WIDTH = 1920;
const BASE_HEIGHT = 1080;

export default function Game1() {
  const router = useRouter();
  const containerRef = useRef();
  const image2Ref = useRef(); // 1-2.png 的 ref
  const [scale, setScale] = useState(1);
  const [result, setResult] = useState(null);
  const [pos, setPos] = useState({ x: 0, y: 0 });

  // ✅ 調整為 false 可隱藏正確點擊區域（紅圈）
  const debug = true;

  // 畫面縮放與置中
  useEffect(() => {
    const resize = () => {
      const container = containerRef.current;
      const scaleX = window.innerWidth / BASE_WIDTH;
      const scaleY = window.innerHeight / BASE_HEIGHT;
      const newScale = Math.min(scaleX, scaleY);
      const offsetX = (window.innerWidth - BASE_WIDTH * newScale) / 2;
      const offsetY = (window.innerHeight - BASE_HEIGHT * newScale) / 2;

      container.style.transform = `scale(${newScale})`;
      container.style.left = `${offsetX}px`;
      container.style.top = `${offsetY}px`;

      setScale(newScale);
    };
    resize();
    window.addEventListener('resize', resize);
    return () => window.removeEventListener('resize', resize);
  }, []);

  const handleCorrectClick = (e) => {
    e.stopPropagation();
    const rect = e.currentTarget.getBoundingClientRect();
    const x = (rect.left + rect.width / 2 - containerRef.current.getBoundingClientRect().left) / scale;
    const y = (rect.top + rect.height / 2 - containerRef.current.getBoundingClientRect().top) / scale;

    setPos({ x, y });
    setResult('O');
    router.push('/game/game3');
  };

  const handleWrongClick = (e) => {
    if (result || !image2Ref.current) return;

    const imageRect = image2Ref.current.getBoundingClientRect();
    const clickX = e.clientX;
    const clickY = e.clientY;

    // 只允許在 1-2.png 圖片區域內觸發叉叉
    if (
      clickX >= imageRect.left &&
      clickX <= imageRect.right &&
      clickY >= imageRect.top &&
      clickY <= imageRect.bottom
    ) {
      const containerRect = containerRef.current.getBoundingClientRect();
      const x = (clickX - containerRect.left) / scale;
      const y = (clickY - containerRect.top) / scale;

      setPos({ x, y });
      setResult('X');
      setTimeout(() => setResult(null), 800);
    }
  };

  return (
    <div
      className="w-screen h-screen relative overflow-hidden"
      style={{ backgroundColor: 'black' }}
      onClick={handleWrongClick}
    >
      <div
        ref={containerRef}
        style={{
          width: `${BASE_WIDTH}px`,
          height: `${BASE_HEIGHT}px`,
          position: 'absolute',
          transformOrigin: 'top left',
          backgroundColor: '#feeac5',
          overflow: 'hidden',
        }}
      >
        {/* 背景 */}
        <img
          src="/photo/bg2.png"
          alt="背景"
          className="absolute"
          style={{
            top: '-17%',
            left: '-45%',
            width: '100%',
            height: 'auto',
            zIndex: 0,
          }}
        />

        {/* 裝飾圖 */}
        <img
          src="/photo/2-1.png"
          alt="裝飾1"
          className="absolute"
          style={{
            top: '3%',
            left: '14%',
            width: '540px',
            height: 'auto',
            zIndex: 1,
          }}
        />
        <img
          ref={image2Ref}
          src="/photo/2-2.png"
          alt="裝飾2"
          className="absolute"
          style={{
            top: '0%',
            right: '4%',
            width: 'auto',
            height: '100%',
            zIndex: 1,
            cursor: 'pointer',
          }}
        />

        {/* 正確點擊區域 */}
        <div
          onClick={handleCorrectClick}
          style={{
            position: 'absolute',
            top: '39.5%',
            left: '87.5%',
            width: '110px',
            height: '110px',
            backgroundColor: debug ? 'rgba(255, 0, 0, 0.3)' : 'transparent',
            borderRadius: '50%',
            zIndex: 5,
            cursor: 'pointer',
          }}
        />

        {/* 顯示 O / X */}
        {result && (
          <img
            src={`/photo/${result}.png`}
            alt={result}
            className="absolute pointer-events-none"
            style={{
              top: `${pos.y}px`,
              left: `${pos.x}px`,
              transform: 'translate(-50%, -50%)',
              width: '120px',
              height: 'auto',
              zIndex: 999,
            }}
          />
        )}
      </div>
    </div>
  );
}
